/* src/index.css */

/*
  此檔案僅包含無法直接由 Tailwind CSS CDN 提供的自訂或基礎樣式。
  所有 Tailwind class 都應直接寫在 React 組件的 className 屬性中。
*/

/* 確保 body 和 root 元素佔滿整個視窗，並處理溢出 */
body, #root {
  margin: 0;
  padding: 0;
  overflow: hidden; /* 防止頁面滾動 */
  height: 100vh; /* 確保佔滿整個視窗高度 */
  width: 100vw;  /* 確保佔滿整個視窗寬度 */
}

